.container {
  font-size: 24px;
}

.top {
  display: grid;
  text-align: center;
  border-bottom: 1px solid gray;
}

.nav > ol {
  margin-bottom: 0;
}

.article {
  margin: 20px;
  margin-top: 0;
}

.btn {
  border: 0;
  padding: 0;
  text-align: center;
  background-color: #0000;
}

.nav > .btn {
  color: gray;
  font-size: 24px;
  margin-left: 40px;
}

.article > h2 {
  margin-top: 24px;
}

.nav {
  overflow: hidden;
}

.top > .btn {
  margin-top: auto;
  margin-bottom: auto;

  width: 40px;
  height: 40px;

  font-size: 32px;
  font-weight: bold;

  border-radius: 12px;

  margin-right: 10px;
  margin-left: 10px;
}

#theme {
  justify-self: end;
}

/* mobile */
@media (hover: none) and (pointer: coarse) {
  #pc {
    display: none;
  }

  .top {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .top h1 {
    white-space: nowrap;
  }

  .article {
    height: 500px;
  }

  .title a {
    margin-left: 5px;
    font-size: 25px;
  }
}

/* pc */
@media (hover: hover) and (pointer: fine) {
  #mobile {
    display: none;
  }

  .container {
    display: grid;
    grid-template-columns: 150px 1fr;
  }

  .top {
    margin-right: 10px;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .nav {
    border-right: 1px solid gray;
    height: 1000px;
  }
}
